	module.exports = {"data":{"markdownRemark":{"html":"<h2>HTML5 Audio</h2>\n<p>Before HTML5, audio files had to be played in a browser using a plug-in like Adobe Flash.\nThe HTML <audio> element is used to embed sound content in documents. It may contain one or more audio sources, represented using the src attribute or the <a href=\"source\">source</a> element</p>\n<p>The following code snippet adds an audio file with the filename <code>tutorial.ogg</code> or <code>tutorial.mp3</code>. The <source> element indicates alternative audio files which the browser may choose from. The browser will utilize the first recognized format. </p>\n<h4>Example 1</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>audio</span> <span class=\"token attr-name\">controls</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>source</span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>tutorial.ogg<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>audio/ogg<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>source</span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>tutorial.mp3<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>audio/mpeg<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\nYour browser does not support the audio element.\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>audio</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<h4>Example 2</h4>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>audio</span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://s3.amazonaws.com/freecodecamp/simonSound1.mp3<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">controls</span> <span class=\"token attr-name\">loop</span> <span class=\"token attr-name\">autoplay</span><span class=\"token punctuation\">></span></span> \n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>audio</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>The <code>controls</code> attribute includes audio controls like play, pause, and volume. If you don’t use this attribute, then no controls will be shown.</p>\n<p>The <code>&#x3C;source></code> element enables you to indicate alternative audio files which the browser may choose from. The browser will utilize the first recognize format.\nThe text between the <code>&#x3C;audio></code> and <code>&#x3C;/audio></code> tags may be shown in browser that does not support the HTML5 <code>&#x3C;audio></code> element.</p>\n<p>The autoplay attribute will automatically play your audio file in the background. It is considered better practice to let visitors choose to play audio.</p>\n<p>The preload attribute indicates what the browser should do if the player is not set to autoplay.</p>\n<p>The loop attribute will play your audio file in a continous loop if mentioned</p>\n<p>Since this is html5, some browser do not support it. You can check it at <a href=\"https://caniuse.com/#search=audio\">https://caniuse.com/#search=audio</a></p>\n<h4>More Information:</h4>\n<p><a href=\"https://caniuse.com/#search=audio\">https://caniuse.com/#search=audio</a></p>\n<p><a href=\"https://www.w3schools.com/html/html5_audio.asp\">https://www.w3schools.com/html/html5_audio.asp</a></p>\n<p><a href=\"https://msdn.microsoft.com/en-us/library/gg589529(v=vs.85).aspx\">https://msdn.microsoft.com/en-us/library/gg589529(v=vs.85).aspx</a></p>","fields":{"slug":"/html/html5-audio/"},"frontmatter":{"title":"HTML5 Audio"}}},"pathContext":{"slug":"/html/html5-audio/"}}

